import React, { useEffect, useState } from "react"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import PropTypes from "prop-types"
import Axios from "../../axios"
import btnArrow from "../images/home/btn-arrow.svg"
import btnArrow1 from "../images/home/btn-arrow-1.svg"


const query = graphql`
    {
      allStrapiExperience {
        nodes {
          year
          locations
          patents
          clients
        }
      }

    }
`

function Experience(props) {

  const data = useStaticQuery(query)
  const experience = data.allStrapiExperience.nodes[0]

  // const [ExperienceHook, setExperienceHook] = useState({})
  // const [ServiceCat, setServiceCat] = useState([])
  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/experience")
  //     const serviceCategories = await Axios.get("/service-categories")
  //     setServiceCat(serviceCategories.data)
  //     setExperienceHook(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])

  const [feedbackName, setFeedbackName] = useState("")
  const [feedbackEmail, setFeedbackEmail] = useState("")
  const [feedbackService, setFeedbackService] = useState("-1")
  const [feedbackDescription, setFeedbackDescription] = useState("")

  const handleFeedbackName = e => {
    setFeedbackName(e.target.value)
  }
  const handleFeedbackEmail = e => {
    setFeedbackEmail(e.target.value)
  }
  const handleFeedbackService = e => {
    setFeedbackService(e.target.value)
  }
  const handleFeedbackDescription = e => {
    setFeedbackDescription(e.target.value)
  }

  const sendFeedback = () => {
    if (!feedbackName || feedbackName == "") {
      alert("Name should not be empty")
      return
    }
    if (!feedbackEmail || feedbackEmail == "") {
      alert("Please enter valid email address")
      return
    }
    if (feedbackService == "-1") {
      setFeedbackService("NOT SPECIFIED")
    }

    const data = {
      name: feedbackName,
      email: feedbackEmail,
      service: feedbackService,
      description: feedbackDescription,
    }

    Axios.post("/messages", data)
      .then(res => {
        alert("send successfully")
        setFeedbackName("")
        setFeedbackEmail("")
        setFeedbackService("")
        setFeedbackDescription("")
      })
      .catch(err => {
        alert("submitted successfully")
       
        
      })
  }

  const validateEmail = email => {
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(email)
  }

  return (
    <section id="count">
      <section className="container-fluid bg-msar-dark bg-msar-count">
        <section className="row">
          <section className="container text-white">
            <section className="row mx-2 mt-3 mt-md-0">
              <section className="col-md-8">
                <section className="row my-md-5">
                  <section className="col-md-6 col-6">
                    <h1>{experience?.year}</h1>
                    <div>Years Experience</div>
                  </section>
                  <section className="col-md-6 col-6">
                    <h1>{experience?.clients}</h1>
                    <div>Clients</div>
                  </section>
                </section>
                <section className="row my-md-5">
                  <section className="ol-md-6 col-6">
                    <h1>{experience?.locations}</h1>
                    <div>Locations</div>
                  </section>
                  <section className="col-md-6 col-6">
                    <h1>{experience?.patents}</h1>
                    <div>Partners</div>
                  </section>
                </section>
              </section>
              <section id="quote" className="col-md-4 bg-msar msar-quote-form">
                <section className="row">
                  <section className="col-md-12">
                    <div className="mt-4">Get Your Free</div>
                    <h4>Consultation &amp; Quote</h4>
                    <div id="frm-quote">
                      <div className="form-group mt-4">
                        <input
                          type="text"
                          className="form-control w-100 border-0"
                          placeholder="Name*"
                          value={feedbackName}
                          onChange={handleFeedbackName}
                          name
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control w-100 border-0"
                          placeholder="Email*"
                          value={feedbackEmail}
                          onChange={handleFeedbackEmail}
                          name
                        />
                      </div>
                      {/* <div className="form-group">
                        <select
                          onChange={handleFeedbackService}
                          value={feedbackService}
                          className="form-control w-100 border-0"
                        >
                          <option value="-1">Service </option>
                          {ServiceCat.map(document => (
                            <option value={document.name} key={document._id}>
                              {document.name}
                            </option>
                          ))}
                        </select>
                      </div> */}
                      <div className="form-group mb-0">
                        <textarea
                          className="form-group w-100 border-0 p-2 mb-0"
                          rows={2}
                          placeholder="Your Text"
                          value={feedbackDescription}
                          onChange={handleFeedbackDescription}
                        />
                      </div>
                      <div className="mt-0 pt-0 small">
                        * Need to fill marked. Make a call more details
                        required.
                      </div>
                      <button
                        type="submit"
                        className="btn-white btn btn-sm mt-3"
                        onClick={sendFeedback}
                      >
                        Send Message <img src={btnArrow1} className="ml-1" />
                      </button>
                    </div>
                  </section>
                </section>
              </section>
            </section>
          </section>
        </section>
      </section>
    </section>
  )
}

Experience.propTypes = {}

export default Experience
