import React, { useEffect, useState } from "react"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import PropTypes from "prop-types"
import Axios from "../../axios"
import { BASE_URL } from "../../config"


const query = graphql`
    {
      allStrapiIndustries {
        nodes {
          name
          id
          img {
            localFile {
              childImageSharp {
                original {
                  src
                }
              }
            }
          }
        }
      }

    }
`


function Industries(props) {

  const data = useStaticQuery(query)
  const industries = data.allStrapiIndustries.nodes

  // const [Industries, setIndustries] = useState([])
  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/industries")
  //     setIndustries(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])
  return (
    <section id="industry">
      <section className="container-fluid bg-msar bg-industry-img">
        <section className="row">
          <section className="container">
            <section className="com-md-12">
              <h1 className="h-industry" align="center">
                Industries
              </h1>
              <div class="line-white"></div>
              {/* <div className="hr">
                <hr className="hr-msar-dark hr-industry" style={{width:`${10*22}px`}} />
              </div> */}
              <section className="row mx-4 mt-4">
                {industries.map(document => (
                  <section key={document.id} className="col-md-3 p-1">
                    <div className="industry-box">
                      <div className="industry-img">
                        <img
                          // src={`${BASE_URL}${document?.img?.url}`}
                          src={document.img.localFile.childImageSharp?.original.src}
                          className="img-fluid"
                        />
                      </div>
                      <div className="industry-layer-box"></div>
                      <div className="industry-name">{document?.name}</div>
                    </div>
                  </section>
                ))}
              </section>
            </section>
          </section>
        </section>
      </section>
    </section>
  )
}

Industries.propTypes = {}

export default Industries
