import React, { useState, useEffect } from "react"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import PropTypes from "prop-types"
import Axios from "../../axios"


const query = graphql`
    {
      allStrapiOffices {
        nodes {
          address
          email
          fax
          mapLink
          name
          phone
        }
      }

    }
`


function Office(props) {

  const data = useStaticQuery(query)
  const offices = data.allStrapiOffices.nodes

  // const [Offices, setOffices] = useState([])

  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/offices")
  //     console.log(request)
  //     setOffices(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])

  const [ActiveMapIndex, setActiveMapIndex] = useState(0)

  return (
    <section id="map">
      <section className="container">
        <section className="row">
          <section className="col-lg-12">
            <div className="text-center">
              {/* <button className="btn btn-sm text-white font-weight-light bg-msar-dark text-uppercase">
                We are eastern Msar
              </button> */}
            </div>
            <h3 align="center" className=" font-weight-bold m-2">
              Our Presence
            </h3>
            <div className="hr">
              <hr className="hr-msar-dark hr-map" style={{width:`${12 * 16}px`}} />
            </div>
          </section>
        </section>
        <section className="row mx-0 mx-lg-5">
          <section className="col-lg-5 mt-5 order-2 order-lg-1">
            {offices.map((document, index) => (
              <div
                key={index}
                onClick={() => setActiveMapIndex(index)}
                className={
                  index == ActiveMapIndex
                    ? `map-box shadow mb-3 active`
                    : `map-box shadow mb-3 `
                }
              >
                <div className="map-name">
                  <h6 className="text-msar-dark m-0">{document?.name}</h6>
                </div>
                <div className="map-name">
                  <p className="m-0">{document?.address}</p>
                </div>
                <div className="row">
                  <div className="col-md-6">
                    <span className="map-tel text-msar-dark">Tel : </span>
                    <span className="map-tel-data"> {document?.phone} </span>
                  </div>
                  <div className="col-md-6">
                    <span className="map-fax text-msar-dark">
                      {document?.fax ? "Fax" : ""}
                    </span>
                    <span className="map-data"> {document?.fax} </span>
                  </div>
                  <div className="col-lg-12">
                    <div className="map-email-box">
                      <span className="map-email text-msar-dark">Email :</span>
                      <span className="map-email-data ml-1">
                        {document?.email}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="arrow-right" />
              </div>
            ))}
          </section>
          <section className="col-lg-7 order-1 order-lg-2">
            <div className="mt-5">
              <iframe
                src={offices[ActiveMapIndex]?.mapLink}
                width="100%"
                height={520}
                frameBorder={0}
                style={{ border: 0 }}
                allowFullScreen
                aria-hidden="false"
                tabIndex={0}
              />
            </div>
          </section>
        </section>
      </section>
    </section>
  )
}

Office.propTypes = {}

export default Office
